<template>
  <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" fullscreen @close="resetForm">
    <el-form ref="dialogForm" :model="dialogForm" label-width="160px" :rules="rules">
      <el-row v-if="isAdd" :gutter="15">
        <el-col :span="12">
          <el-form-item label="涉及路线" prop="lineId">
            <el-select placeholder="请选择涉及路线" v-model="dialogForm.lineId" filterable @change="handleChangeCode">
              <el-option v-for="item in lineOption" :key="item.id" :label="item.name" :value="item.id">
                <span style="float: left">{{ item.name }}</span>
                <span style="float: right; color: #8492a6; font-size: 13px">线路id:{{ item.id }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col :span="8">
          <el-form-item label="案(事)件编号">
            <el-input v-model="dialogForm.caseNo" placeholder="请填写案(事)件编号" maxlength="20"></el-input>
          </el-form-item>
          <el-form-item label="主犯(嫌疑人)证件类型" prop="suspectsIdcode">
            <el-select placeholder="请选择证件类型" v-model="dialogForm.suspectsIdcode">
              <el-option v-for="(item, index) in dic['证件代码']" :key="index" :label="item.name" :value="item.code"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发生日期" prop="happenDate">
            <el-date-picker v-model="dialogForm.happenDate" type="date" placeholder="请选择发生日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="作案人数">
            <el-input v-model="dialogForm.casePeopleCount" placeholder="请填写作案人数" maxlength="200"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="案(事)件名称" prop="name">
            <el-input v-model="dialogForm.name" placeholder="请填写案(事)件名称" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="主犯(嫌疑人)证件号码" prop="suspectsCardNo">
            <el-input v-model="dialogForm.suspectsCardNo" placeholder="请填写主犯(嫌疑人)证件号码" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="发生地点" prop="happenPlace">
            <el-input v-model="dialogForm.happenPlace" placeholder="请填写发生地点" maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="抓捕人数">
            <el-input v-model="dialogForm.arrestCount" placeholder="请填写抓捕人数" maxlength="50"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="案件性质" prop="nature">
            <el-select placeholder="请选择案件性质" v-model="dialogForm.nature">
              <el-option v-for="(item, index) in dic['案件性质']" :key="index" :label="item.name" :value="item.code"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="主犯(嫌疑人)姓名" prop="suspectsName">
            <el-input v-model="dialogForm.suspectsName" placeholder="主犯(嫌疑人)证件姓名" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="是否破案">
            <el-radio-group v-model="dialogForm.isCaseSolve">
              <el-radio :label="0">否</el-radio>
              <el-radio :label="1">是</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="在逃人数">
            <el-input v-model="dialogForm.escapedCount" placeholder="请填写在逃人数" maxlength="50"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col :span="16">
          <el-form-item label="案(事)件情况" prop="caseDesc">
            <el-input v-model="dialogForm.caseDesc" type="textarea" :rows="3" placeholder="请填写治安隐患情况" maxlength="4000"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="15">
        <el-col :span="16">
          <el-form-item label="案件侦破情况" prop="detectedDesc">
            <el-input v-model="dialogForm.detectedDesc" type="textarea" :rows="3" placeholder="请填写治安隐患情况" maxlength="4000"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="warning" size="small" @click="resetForm">取消</el-button>
      <el-button type="success" size="small" @click="submitForm">保存</el-button>
    </div>
  </el-dialog>
</template>
<script>
import service from "./services/roadline_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      lineOption: [],
      dic: {
        案件性质: [],
        证件代码: []
      },
      dialogForm: {
        isCaseSolve: 0,
        districtCode: "",
        suspectsIdcode: "111"
      },
      rules: {
        lineId: [
          {
            required: true,
            message: "请选择涉及线路",
            trigger: "change"
          }
        ],
        name: [
          {
            required: true,
            message: "请输入案卷名称",
            trigger: "blur"
          }
        ],
        happenDate: [
          {
            required: true,
            message: "请选择发生日期",
            trigger: "change"
          }
        ],
        happenPlace: [
          {
            required: true,
            message: "请输入发生地点",
            trigger: "blur"
          }
        ],
        nature: [
          {
            required: true,
            message: "请选择案(事)件性质",
            trigger: "change"
          }
        ],
        caseDesc: [
          {
            required: true,
            message: "请输入案(事)件情况",
            trigger: "blur"
          }
        ],
        suspectsIdcode: [
          {
            required: true,
            message: "请输入选择(嫌疑人)证件类型",
            trigger: "change"
          }
        ],
        suspectsCardNo: [
          {
            required: true,
            message: "请输入主犯(嫌疑人)证件号码",
            trigger: "blur"
          }
        ],
        suspectsName: [
          {
            required: true,
            message: "请输入主犯(嫌疑人)姓名",
            trigger: "blur"
          }
        ],
        detectedDesc: [
          {
            required: true,
            message: "请输入案件侦破情况",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  watch: {
    "dialogForm.districtCode": {
      handler(code) {
        if (code) {
          service.requestCodeByAddress(code, data => {
            if (data.belongname) {
              this.dialogForm.happenPlace = data.belongname + data.name;
            } else {
              this.dialogForm.happenPlace = "铜川市";
            }
          });
        }
      }
    }
  },
  methods: {
    handleChangeCode(id) {
      if (id) {
        let code = this.lineOption.find(item => item.id == id).districtCode;
        // 根据选中项重新获取区域编码
        this.dialogForm.districtCode = code;
      }
    },
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          param.happenDate = util.time.formatDate(
            param.happenDate,
            "YYYY-MM-DD"
          );
          if (this.isAdd == true) {
            // 添加
            service.requestSaveLineCase(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "案卷信息添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateLineCase(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "案卷信息修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        isCaseSolve: 0,
        districtCode: "",
        suspectsIdcode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}
</style>