import request from '@/plugin/axios';
import publicService from "@/api/sys/service.js";

const service = {
    ...publicService,
    /**
     * 获取护路护线
     * 
     * @param {any} successFunc 回调函数
     */
    requestLine(id,successFunc) {
        request({
            url: "/compre/peace/roadline/find?id="+id,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页获取护路护线列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageLineProte(param, successFunc) {
        request({
            url: "/compre/peace/roadline/findPage?name=" + param.queryForm.name + "&districtCode=" + param.queryForm.districtCode + "&page=" + param.page + "&pageSize=" + param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 根据区域编码获取护路护线
     * 
     * @param {any} code 区域编码
     * @param {any} successFunc 回调函数
     */
    requestLineProteByCode(code, successFunc){
        request({
            url: "/compre/peace/roadline/findPage?&districtCode="+ code,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加护路护线
     * 
     * @param {any} param 护路护线参数
     * @param {any} successFunc 回调函数
     */
    requestSaveLineProte(param, successFunc) {
        request({
            url: "/compre/peace/roadline/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改护路护线
     * 
     * @param {any} param 护路护线参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateLineProte(param, successFunc) {
        request({
            url: "/compre/peace/roadline/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除护路护线
     * 
     * @param {any} peopleId 护路护线 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteLineProte(peopleId, successFunc) {
        request({
            url: "/compre/peace/roadline/delete?id=" + peopleId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 分页获取涉及线路案件列表
     * 
     * @param {any} param 分页参数
     * @param {any} successFunc 回调函数
     */
    requestPageLineCase(param, successFunc) {
        request({
            url: "/compre/peace/case/findPage?name=" + param.queryForm.name + "&districtCode=" + param.queryForm.districtCode + "&lineId=" + param.queryForm.lineId + "&page=" + param.page + "&pageSize=" + param.pageSize,
            method: "get",
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 添加涉及线路案件
     * 
     * @param {any} param 涉及线路案件参数
     * @param {any} successFunc 回调函数
     */
    requestSaveLineCase(param, successFunc) {
        request({
            url: "/compre/peace/case/save",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 修改涉及线路案件
     * 
     * @param {any} param 涉及线路案件参数
     * @param {any} successFunc 回调函数
     */
    requestUpdateLineCase(param, successFunc) {
        request({
            url: "/compre/peace/case/update",
            method: "post",
            data: param,
        }).then((data) => {
            successFunc(data);
        })
    },
    /**
     * 删除涉及线路案件
     * 
     * @param {any} peopleId 涉及线路案件 ID
     * @param {any} successFunc 回调函数
     */
    requestDeleteLineCase(peopleId, successFunc) {
        request({
            url: "/compre/peace/case/delete?id=" + peopleId,
            method: "post",
        }).then((data) => {
            successFunc(data);
        })
    }
};

export default service;