<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
          @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
          @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-4">涉及线路：</span>
                        <div class="techo-list__header-col-left-4">
                            <el-select placeholder="请选择涉及线路" size="small" clearable filterable v-model="queryForm.lineId">
                                <el-option v-for="(item, index) in lineOption" :label="item.name" :value="item.id" :key="index"></el-option>
                            </el-select>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-4">案卷名称：</span>
                        <div class="techo-list__header-col-left-4">
                        <el-input placeholder="请输入案件名称" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :title="title"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/roadline_service.js";
import vAddDialog from "./lineroad-case-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "案卷",
      queryForm: {
        districtCode: "",
        name: "",
        lineId: ""
      },
      header: {
        // "案(事)件编号": "caseNo",
        "案(事)件名称": "name",
        发生日期: "happenDate",
        发生地点: "happenPlace",
        "案(事)件性质": "dicMap.nature",
        // "案(事)件情况": "caseDesc",
        // "主犯(嫌疑人)证件类型": "dicMap.suspectsIdcode",
        // "主犯(嫌疑人)证件号码": "suspectsCardNo",
        "主犯(嫌疑人)姓名": "suspectsName",
        是否破案: "dicMap.isCaseSolve"
        // 作案人数: "casePeopleCount",
        // 在逃人数: "escapedCount",
        // 抓捕人数: "arrestCount",
        // 案件侦破情况: "detectedDesc",
        // 区域代码: "districtCode"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      lineOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "lineCase"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
    /** 根据区域编码获取护路护线 */
    service.requestLineProteByCode(this.queryForm.districtCode, data => {
      this.lineOption = data.list;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    },
    "queryForm.districtCode": {
      handler(code) {
        service.requestLineProteByCode(code, data => {
          this.lineOption = data.list;
        });
      }
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加案卷 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
      this.$refs["vAddDialog"].lineOption = this.lineOption;
    },
    /** 删除案卷 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteLineCase(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "案卷删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "案卷删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo.queryForm = _.cloneDeep(this.queryForm);
      service.requestPageLineCase(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageLineCase(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              item.dicMap &&
                (item.dicMap.isCaseSolve = item.isCaseSolve == 1 ? "是" : "否");
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            item.dicMap &&
              (item.dicMap.isCaseSolve = item.isCaseSolve == 1 ? "是" : "否");
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
